<template>
  <v-container class="fill-height" justify-center fluid>
    <v-row align="center" justify="center">
      <v-col cols="12" sm="8" md="4">
        <v-card class="elevation-12" v-if="0===state">
          <v-toolbar color="primary" dark flat>
            <v-toolbar-title>Register</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form>
              <v-text-field label="Email" name="email"
                prepend-icon="mdi-email" type="email" clearable
                v-model="email" :error="emailErr"
              />
              <v-text-field label="Name" name="name"
                prepend-icon="mdi-account" type="text" clearable
                v-model="name"
              />
              <v-text-field id="password" label="Password" name="password"
                prepend-icon="mdi-lock" type="password" clearable
                v-model="password" :error="pwdErr"
              />
              <v-text-field id="cnfpassword" label="Re-type password" name="cnfpassword"
                prepend-icon="mdi-lock" type="password" clearable
                v-model="cnfpassword" :error="pwdErr"
              />
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-btn text color="primary" small to="/login"
              :disabled="loading"
            >Login</v-btn>
            <v-spacer />
            <v-btn color="primary" @click="btnRegClick" :loading="loading"
              :disabled="!inputOK"
            >Register</v-btn>
          </v-card-actions>
        </v-card>
        <v-card class="elevation-12" v-if="1===state">
          <v-card-text>
            An email with verification link has been sent to email address
            <strong>{{ email }}</strong>. Please check the email, then click
            the verification link so you can login to our service.
          </v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn text color="primary" to="/login">Go to login screen</v-btn>
          </v-card-actions>
        </v-card>
        <v-card class="elevation-12" v-if="2===state">
          <v-card-text>
            Ups! Something went wrong!!!
          </v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn text color="primary" @click="state=0">Try again</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
"use strict"

import { register } from "@/lib/auth"
import { validateEmail } from "@/lib/utils"

export default {
  name: "Register",
  data: () => ({
    email: "",
    name: "",
    password: "",
    cnfpassword: "",
    state: 0,
    loading: false,
    emailErr: false,
    pwdErr: false,
  }),
  computed: {
    inputOK() {
      try {
        if (0 === this.email.length) return false
        if (0 === this.name.length) return false
        if (0 === this.password.length) return false
        if (0 === this.cnfpassword.length) return false
        return true
      } catch {
        return false
      }
    },
  },
  methods: {
    btnRegClick: async function() {
      this.emailErr = false
      this.pwdErr = false
      if (!validateEmail(this.email)) {
        this.emailErr = true
        return
      }
      if (this.password!==this.cnfpassword) {
        this.pwdErr = true
        return
      }
      this.loading = true
      let resp = await register(this.email, this.name, this.password)
      this.loading = false
      if (resp.code===200) {
        this.state = 1
      } else {
        this.state = 2
        console.log(resp)
      }
    },
  }
}
</script>